<template>
  <div class="container">
    <div class="banner-wrap">
      <img src="../../images/home/banner.png" alt="">
    </div>
    <div class="card-wrap">
      <div class="title-wrap">
        <div class="title">开放API</div>
        <div class="sub-title">提供标准化的对外访问接口，帮助合作伙伴快速接入弘昊开放平台</div>
      </div>
      <div class="card">
        <div class="card-item" v-for="(item,index) in list" :key="index">
          <img class="card-logo" :src="item.logo" alt="">
          <div class="card-title">{{item.name}}</div>
          <div class="card-msg">
            <div>{{item.msg1}}</div>
            <div>{{item.msg2}}</div>
            <div>{{item.msg3}}</div>
          </div>
          <router-link to class="more">
            更多>>
          </router-link>
        </div>
      </div>
    </div>
    <div class="card2">
      <img src="../../images/home/rect.png" alt="">
      <div class="btn-wrap">
        <button @click="$router.push({name: 'register'})">注册并认证</button>
        <button>合作方接入指南</button>
      </div>
    </div>
    <dialogWrap ref="dialog" class="dialog-wrap">
      <div class="dialog">
        <div class="title">您尚未认证，是否去认证？</div>
        <div class="btn-wrap">
          <button @click="$refs.dialog.visible = false">取消</button>
          <button class="common-btn" @click="doCertification">确定</button>
        </div>
      </div>
    </dialogWrap>
  </div>
</template>

<script>
import dialogWrap from '../../components/common/dialog'
export default {
  components:{
    dialogWrap
  },
  data() {
    return {
      list:[
        {
          name: '浦望贷',
          msg1: '银行牌价查询',
          msg2: '外汇买卖申请明细查询',
          msg3: '外汇贷款业务付汇结算',
          logo: require('../../images/home/card1.png')
        },
        {
          name: '供应链快贷',
          msg1: '银行牌价查询',
          msg2: '外汇买卖申请明细查询',
          msg3: '外汇贷款业务付汇结算',
          logo: require('../../images/home/card2.png')
        },
        {
          name: '电子账户',
          msg1: '银行牌价查询',
          msg2: '外汇买卖申请明细查询',
          msg3: '外汇贷款业务付汇结算',
          logo: require('../../images/home/card3.png')
        },
        {
          name: '担保协议',
          msg1: '银行牌价查询',
          msg2: '外汇买卖申请明细查询',
          msg3: '外汇贷款业务付汇结算',
          logo: require('../../images/home/card4.png')
        }
      ]
    }
  },
  methods:{
    doCertification() {
      this.$router.push({name: 'certification'})
    }
  }
}
</script>

<style lang="less" scoped>
.banner-wrap {
  margin: 20px 0 34px 0;
  img {
     width: 100%;
     display: block;
  }
}
.card-wrap {
  background: #fff;
  border: 1px solid #E5E5E5;
  margin-bottom: 30px;
  .title-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 127px;
    .title {
      font-size: 23.64px;
      color: #030303;
      font-weight: 500;
      margin-bottom: 20px;
    }
    .sub-title {
      font-size: 16px;
      color: #6A6A6A;
    }
  }
  .card {
    display: flex;
    .card-item{
      width: 25%;
      text-align: center;
      padding-top: 26px;
      padding-bottom: 29px;
      position: relative;
      .card-logo {
        width: 83px;
        height: 83px;
        display: block;
        margin: 0 auto;
      }
      .card-title {
        color: #3C3C3C;
        font-size: 16px;
        font-weight: 500;
        line-height: 16px;
        margin: 26px 0;     
      }
      .card-msg {
        height: 74px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        div {
          color: #787878;
          font-size: 14px;
          line-height: 14px;
        }
        margin-bottom: 35px;
      }
      .more {
        color: #646464;
        font-size: 16px;
        text-decoration: none;
        &:hover {
          color: #B90909;
        }
      }
      &:nth-of-type(1) {
        border-top: 6px solid #5B88F4;
      }
      &:nth-of-type(2) {
        border-top: 6px solid #34B9AA;
      }
      &:nth-of-type(3) {
        border-top: 6px solid #FF6869;
      }
      &:nth-of-type(4) {
        border-top: 6px solid #FDC552;
      }
      &:not(:last-child) {
        border-right: 1px solid #E5E5E5;
      }
    }
  }
}
.card2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 365px;
  padding: 0 65px;
  box-sizing: border-box;
  background-color: #fff;
  margin-bottom: 36px;
  img {
    width: 820px;
    display: block;
  }
  .btn-wrap{
    button {
      width: 183px;
      height: 41px;
      line-height: 41px;
      text-align: center;
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      background: #B90909;
      border-radius: 4px;
      border: 0;
      display: block;
      cursor: pointer;
    }
    & > button:first-child {
      margin-bottom: 38px;
    }
  }
}
.dialog-wrap {
  /deep/ .el-dialog {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 0!important;
  }
}
.dialog {
  .title {
    text-align: center;
    color: #1F1E1E;
    font-size: 16px;
    font-weight: 500;
  }
  .btn-wrap {
    display: flex;
    justify-content: center;
  }
  button{
    width: 169px;
    height: 40px;
    margin-top: 42px;
    cursor: pointer;
    &:first-child {
      margin-right: 17px;
      background: #fff;
      border: 1px solid #BFBFBF;
      border-radius: 4px;
      color: #5E5E5E;
      font-size: 14px;
    }
  }
}
</style>


